	&.main {
		width: 100%;
		height: 80px;
		position: absolute;
		top: 0;
		left: 0px;
		background: transparent;
		border-bottom: 1px solid #ccc;
		z-index: 2;

		.inner {
			width: 100%;
			height: 100%;

			h1 {
				left: 5vw;
				a {
					font-size: 30px;
					color: #fff;
				}
			}
			#gnb {
				right: 5vw;
				li a {
					color: #222;
				}
			}
			.fa-bars {
				color: #fff;
			}
		}
	}
	&.sub {
		background: #222;
		.inner {
			h1 a {
				color: #fff;
			}
			#gnb li a {
				color: #fff;
			}
		}
	}
}

@media screen and (max-width: $tablet) {
	header {
		.inner {
			width: 100%;
			h1 {
				left: 40px;
			}
			#gnb {
				display: none;
			}
			.fa-bars {
				display: block;
			}
		}
	}
}
